@import url(https://fonts.googleapis.com/css2?family=Allura&family=Benne&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  background-color: black;
}
.App {
  text-align: center;
  height: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  /* background-color: #f0eeed; */
  background-color: black;
  color: #d0c7bd;
}
.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  font-size: calc(10px + 1vmin);
  font-family: "Benne", serif;
  padding-bottom: 50px;
}

.App-header-logo {
  font-size: 80px;
  padding: 30px 0;
}

.App-header-logo > p {
  margin: 0;
}

.App-header-logo > p:first-child {
  font-size: 60px;
  margin: 0;
  font-family: "Allura", cursive;
}

.App-header-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  list-style-type: none;
  font-size: 20px;
  padding: 10px 0;
  margin: 0;
  border-top: 0.5px solid #252525;
}

.App-header-socials {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 20%;
}

.App-header-socials > a {
  font-size: 16px;
}

li {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 40px;
}
a {
  text-decoration: none;
  color: #d0c7bd;
}

.Home {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.block {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 0 50px 50px 50px;
}

.img-flower {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  width: 20%;
  height: auto;
}

.img-horse,
.img-dog {
  width: 40%;
  height: auto;
}

