/* About page */

.About {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px 15%;
}

.About-img {
  padding: 10px;
}

.About-text {
  padding: 50px;
  text-align: left;
  font-size: 18px;
  font-family: "Benne", serif;
}

.About-text > p:first-child {
  font-size: 50px;
  margin: 0px;
}

